.search {
  &.show {
    .search__select::after {
      transform: rotate(-180deg);
    }

    .search__form {
      opacity: 1;
      display: grid;
      z-index: 3;
    }

    .search__select {
      margin-bottom: 16px;
    }
  }
}

.search__form {
  display: none;
  gap: 16px;
  opacity: 0;
  display: none;
  z-index: -1;

  @include tablet {
    opacity: 1;
    display: grid;
    z-index: 1;
    grid-template-columns: 2fr 3fr auto;
  }

  @include laptop {
    grid-template-columns: 1fr 1fr auto;
  }
}

.search.advanced {
  .search__job-title {
    grid-area: job-title;
  }

  .search__area {
    grid-area: job-area;
  }

  .search__submit {
    grid-area: submit;
  }

  .search__location {
    grid-area: location;
  }

  .search__job-type {
    grid-area: job-type;
  }

  .search__company {
    grid-area: company;
  }

  .search__quiz {
    grid-area: quiz;
  }

  .search__form {
    opacity: 1;
    display: grid;
    grid-template-areas:
      "job-title job-title"
      "job-area job-area"
      "location job-type"
      "company company"
      "submit submit"
      "quiz quiz ";
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    align-items: end;

    @include tablet {
      grid-template-areas:
        "job-title job-title job-area job-area"
        "location job-type company company"
        "quiz quiz submit submit";
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }

    @include desktop {
      grid-template-areas:
        "job-title job-title job-area job-area submit"
        "location job-type company quiz quiz";
      grid-template-columns: [job-title] 25% [job-title] 25% [job-area] 1fr [job-area] 1fr [submit] 260px;
      gap: 40px 16px;
    }
  }
}

.search__select {
  width: 100%;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: auto;
    right: 29px;
    bottom: 0;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 11px 8px 0;
    border-color: $color-white transparent transparent transparent;
    margin: auto 0;
    transition: $transition-duration;
    z-index: 1;
  }

  @include tablet {
    display: none;
  }
}
